.configuration-section-card {
  background-color: #ffffff;
  border-radius: 10px;
  width: 100%;
  margin-bottom: 20px;
  padding-top: 25px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 30px;

  .form-section-header {
    color: #3a3541;
    opacity: 0.8;
    margin-bottom: 35px;
    font-weight: bold;
    font-size: 15px;

    &.sectors {
      margin-bottom: 5px;
    }
  }

  .form-section-subheader {
    color: #3a3541;
    opacity: 0.8;
    margin-bottom: 35px;
    font-size: 13px;
  }

  .form-item-header {
    color: #3a3541;
    opacity: 0.8;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .form-input-box {
    font-size: 13px;
    height: 35px;
  }

  .gwp-save-button {
    margin-top: 30px;
    height: 35px;
    width: 90px;
  }
}
